import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article9 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Sport</h1>
      <h2>
        Historia sportu w omy siga koca XIX wieku, kiedy to w 1897 roku
        zorganizowano pierwsze wycigi wiolarskie amatorw. Dwa lata pniej
        rozpoczto starania o powoanie towarzystwa wiolarskiego, ktre to
        zainaugurowao swoj oficjaln dziaalno 26 stycznia 1902 roku pod
        nazw omyskiego Towarzystwa Wiolarskiego. Stowarzyszenie prnie
        dziaao do rozpoczcia II wojny wiatowej. Jego dziaalno zostaa
        zawieszona w latach 80. XX wieku. Kompleks rekreacyjno-sportowy Orlik
        2012 16 kwietnia 1926 roku zosta zaoony pierwszy klub pikarski,
        obecnie trzecioligowy KS oma. Ponadto w miecie dziaa kilkanacie
        klubw sportowych reprezentujcych rne dyscypliny. Do najwaniejszych
        sekcji oprcz piki nonej nale siatkwka, koszykwka, lekkoatletyka
        oraz sporty walki. Swoje najwiksze sukcesy omanie odnosili wanie w
        lekkoatletyce, sportach walki, a take w kulturystyce. W
        czerwcu 2009 roku zorganizowano w omy Oglnopolski Fina Gimnazjady w
        Pice Nonej Chopcw, w ktrym lokalna druyna pikarska z Publicznego
        Gimnazjum nr 8 zaja 3. miejsce. omyski sport wspierany jest
        przez Towarzystwo Krzewienia Kultury Fizycznej oraz omyski Szkolny
        Zwizek Sportowy.
      </h2>
    </BackgroundImage>
  )
}

export default Article9
