import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article1 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Nazwa miasta</h1>
      <h2>
        Geneza nazwy oma nie jest jednoznaczna. Zygmunt Gloger pisze m.in. e:
        Ludzie mieszkajcy w lasach i yjcy z mylistwa, pasterstwa i
        rybowstwa nie mieli potrzeby budowania zamkw, bo przyrodzon i
        najbezpieczniejsz ich warowni byy zawalone omami starych drzew
        gszcze lene. Natomiast wedug prof. Karola Zierhoffera nazwa (oma)
        oznaczaa przypuszczalnie miejsce, gdzie by jaki om, jakie
        pokruszone bloki kamienne bd poamane przez wiatr drzewa. Nazwa miasta
        jest topograficzna, urobiona za pomoc przyrostka a od tego samego
        rdzenia, co staropolskie omi, oma. Podobnie uformowana jest nazwa
        miasta Ia. Nazwa oma wystpuje rwnie w innych jzykach pod
        zmienionymi formami: ac. Lompza, niem. Lomscha, jid. Lomsza, ros.
        , lit. Loma. Jest to zwizane m.in. z wielonarodowociow
        struktur etniczn miasta na przestrzeni dziejw i kontaktami handlowymi
        prowadzonymi przez mieszkacw miasta.
      </h2>
    </BackgroundImage>
  )
}

export default Article1
