import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article2 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Gospodarka</h1>
      <h2>
        Pooenie omy w regionie o charakterze rolniczo-lenym wskazuje na
        podstawowe kierunki rozwoju gospodarczego, ktrymi s brane
        nieuciliwe ekologicznie, tj. przemys spoywczy, browarnictwo,
        elektronika, przemys materiaw budowlanych, drzewny, meblarski,
        produkcja i przetwrstwo podw rolnych, a take turystyka i
        agroturystyka. Na terenie miasta nie istnieje adne due
        przedsibiorstwo zatrudniajce ponad 1000 pracownikw, jednak
        funkcjonuj przedsibiorstwa, ktre znalazy si na Podlaskiej Setce
        Przedsibiorcw. S to m.in. Browar oma 
        , DOMEL,
        FARGOTEX , KONRAD 
        , omyska Fabryka meblarski, PEPEES ,
        UniGlass Polska. Miasto jest siedzib
        Podlaskiej centrali Agencji Restrukturyzacji i Modernizacji Rolnictwa.
        Na koniec 2007 roku liczba pracujcych w miecie oma wynosia 13 408,
        w tym 7170 kobiet, natomiast stopa bezrobocia na koniec wrzenia
        2012 roku wynosia 15,8%. Liczba podmiotw gospodarczych
        zarejestrowanych w rejestrze REGON na koniec 2008 roku wynosia 6421, w
        tym 6280 podmiotw sektora prywatnego. We wrzeniu 2019 liczba
        zarejestrowanych bezrobotnych wynosia 1800 osb, a stopa bezrobocia
        wynosia 7,3%.
      </h2>
    </BackgroundImage>
  )
}

export default Article2
