import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article6 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Ochrona Zdrowia</h1>
      <h2>
        Za ochron zdrowia w powiecie omyskim odpowiada prowadzony przez ten
        powiat i finansowany przez NFZ  Samodzielny Publiczny Zakad Opieki
        Zdrowotnej w omy z siedzib przy ulicy Szosa Zambrowska 1/27. ZOZ
        skada si z izby przyj, pogotowia ratunkowego oraz czterech
        przychodni rejonowych. W omy mieci si rwnie Szpital Wojewdzki
        im. Kardynaa Stefana Wyszyskiego. Uzupenieniem publicznej suby
        zdrowia jest osiem Niepublicznych Zakadw Opieki Zdrowotnej oraz
        placwka terenowa suby zdrowia MSWiA. Poza tym na terenie miasta
        dziaaj Caritas, przychodnie specjalistyczne (np. gabinety
        stomatologiczne) oraz 20 aptek. Natomiast usugi z zakresu
        rehabilitacji wiadczy m.in. Centrum Rehabilitacji Caritas Diecezji
        omyskiej pw. w. Rocha.
      </h2>
    </BackgroundImage>
  )
}

export default Article6
