import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article7 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo5.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Suby Mundurowe</h1>
      <h2>
        omyska Policja wsppracuje z andarmeri Wojskow oraz korzysta z
        systemu monitoringu utworzonego przez miasto. Na terenie miasta znajduje
        si Komenda Miejska z siedzib przy ul. Wojska Polskiego 9 i czci
        sekcji przy ul. Partyzantw 48B. omyska komenda podlega Komendzie
        Wojewdzkiej. Dzieli si na 5 sekcji i 1 referat. Sekcja dzielnicowych
        dzieli miasto na dwa rewiry, czyli cznie 13 rejonw. W miecie
        funkcjonuje take stra miejska. Od 1997 roku w miecie oma
        funkcjonuje program Bezpieczne miasto realizowany we wsppracy z
        Komend Miejsk Policji, Stra Miejsk oraz innymi podmiotami ycia
        spoecznego. W ramach tego programu w 2001 roku uruchomiono miejski
        monitoring polegajcy na umieszczeniu kamer w miejscach wymagajcych
        staego nadzoru. Za bezpieczestwo poarowe odpowiada Komenda
        Miejska Pastwowej Stray Poarnej majca sw siedzib przy ul.
        Sikorskiego 48/94, wspomagana przez Ochotnicz Stra Poarn (10
        jednostek na terenie gminy oma).
      </h2>
    </BackgroundImage>
  )
}

export default Article7
