import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article8 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Religia</h1>
      <h2>
        Mieszkacy omy to gwnie osoby wyznania rzymskokatolickiego, chocia
        na przestrzeni wiekw miasto byo wielonarodowociowe i tym samym
        wielowyznaniowe. Oprcz katolikw miasto zamieszkiwali m.in. wyznawcy
        judaizmu czy te protestanci, o czym wiadcz zabytki architektury
        sakralnej oraz nagrobki na omyskich cmentarzach. Dodatkowym
        potwierdzeniem wielowyznaniowej struktury miasta by fakt, e wanie w
        omy 24 maja 1899 roku Jzef Pisudski dokona konwersji na luteranizm,
        w czym uczestniczy omyski pastor ks. Mikulski. Oprcz osb
        wyznania rzymskokatolickiego w miecie mieszka rwnie dua grupa
        muzumanw pochodzenia czeczeskiego. Osiem omyskich parafii
        rzymskokatolickich skupionych jest w dwa dekanaty: oma  w. Brunona
        oraz oma  w. Michaa Archanioa, ktre obejmuj rwnie dziewi
        okolicznych miejscowoci. Ponadto na terenie miasta dziaalno
        duszpastersk prowadzi Koci Zielonowitkowy w RP  zbr Sowo
        ycia, dwa zbory wiadkw Jehowy (w tym grupa jzyka migowego oraz
        grupa rosyjskojzyczna) oraz neopogaski Rodzimy Koci Polski.
      </h2>
    </BackgroundImage>
  )
}

export default Article8
