import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article5 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Owiata</h1>
      <h2>
        Historia szkolnictwa w omy siga pocztkw XV wieku, kiedy to zaoono
        pierwsz parafi, a razem z ni szko parafialn. W 1614 roku
        przebywajcy na terenie miasta jezuici utworzyli Kolegium Jezuickie.
        Jednym z kolejnych rektorw kolegium by w. Andrzej Bobola. Poziom
        szkolnictwa nie spad po przekazaniu przez KEN szkoy pijarom w 1774
        roku. Pod wadz zaborcw w omy wyksztacio si wiele wanych
        osobistoci, m.in. Szymon Konarski, Rafa Krajewski, Jakub Ignacy Waga,
        Wojciech Szweykowski, Adam Chtnik. Obecnie w omy jest dobrze
        rozwinita sie szk publicznych i niepublicznych na wszystkich
        szczeblach. Znajduje si tutaj 7 szk podstawowych, 8 gimnazjw, 10
        szk ponadgimnazjalnych, 6 szk wyszych (w tym 3 niepubliczne) oraz 2
        szkoy artystyczne. Poziom ksztacenia w omy jest wysoki, o czym
        wiadcz wyniki egzaminw maturalnych i gimnazjalnych w skali kraju i
        wojewdztwa. Na przykad I Liceum Oglnoksztacce naley do krajowej i
        wojewdzkiej czowki szk rednich. Ponadto wiele omyskich
        szk zdobyo tytu Szkoy z klas. Od 1 stycznia 1999 w omy swoj
        siedzib ma Okrgowa Komisja Egzaminacyjna, ktra obejmuje swoj
        dziaalnoci wojewdztwo podlaskie i warmisko-mazurskie.
      </h2>
    </BackgroundImage>
  )
}

export default Article5
