import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Informacje Oglne</h1>
      <h2>
        oma to miasto na prawach powiatu w pnocno-wschodniej Polsce, w
        wojewdztwie podlaskim, nad rzek Narew. Miasto zlokalizowane jest na
        historycznym Mazowszu, stolica dawnej ziemi omyskiej. oma to gwny
        orodek gospodarczy, edukacyjny i kulturowy ziemi omyskiej oraz
        trzecie co do wielkoci miasto wojewdztwa podlaskiego. Miasto
        krlewskie Korony Krlestwa Polskiego w wojewdztwie mazowieckim.
        Miejsce obrad sejmikw ziemskich ziemi omyskiej od XVI wieku do
        pierwszej poowy XVIII wieku. W latach 19751998 bya stolic
        wojewdztwa omyskiego. Od 1 stycznia 1999 roku jest siedzib powiatu
        omyskiego. Od 28 padziernika 1925 jest take stolic diecezji
        omyskiej Kocioa rzymskokatolickiego. Wedug danych zebranych przez
        przedstawicieli Gwnego Urzdu Statystycznego z 30 czerwca 2020 roku,
        miasto liczyo 62 795 mieszkacw.
      </h2>
    </BackgroundImage>
  )
}

export default Article
