import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article10 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Administracja</h1>
      <h2>
        oma jest miastem z prawami powiatu. Mieszkacy omy wybieraj do
        swojej rady miasta 23 radnych. Organem wykonawczym wadz jest prezydent
        miasta. Siedzib wadz miejskich jest ratusz. Miasto jest take siedzib
        starostwa powiatu omyskiego, a take wiejskiej gminy oma. W omy
        znajduje si sd rejonowy, sd okrgowy, a take szereg urzdw rangi
        powiatowej. Mieszkacy omy wybieraj parlamentarzystw z okrgu
        wyborczego Biaystok, a posw do Parlamentu Europejskiego z okrgu
        wyborczego Olsztyn.W latach 19751998 oma bya stolic wojewdztwa
        omyskiego. Wojewdztwo to byo terenem rolniczym o trzeciej
        najniszej gstoci zaludnienia i jednym z najniszych wskanikw
        urbanizacji
      </h2>
    </BackgroundImage>
  )
}

export default Article10
