import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article4 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Transport Kolejowy</h1>
      <h2>
        Poczenia kolejowe z om byy ju planowane w XIX wieku, jednak
        dopiero z pocztkiem I wojny wiatowej i wkroczeniem Rosjan do miasta
        zostao otwarte poczenie niadowo  oma. Rosjanie rozpoczli rwnie
        przygotowywanie terenu pod tor kolejowy w kierunku Kolna, jednak w
        sierpniu 1915 opucili miasto i prace zostay wstrzymane. Stworzenia
        poczenia pomidzy Kolnem a om podjli si rwnie Niemcy, jednak
        bezowocnie. W latach 19221927 podejmowano kolejne prby stworzenia
        omawianego odcinka, jednak do budowy nigdy nie przystpiono. W latach
        19171925 z omy do Warszawy kursoway trzy pocigi dziennie. Ponadto
        od 1921 roku otwarto kolej wskotorow na odcinku oma  Nowogrd,
        ktra bya czci trasy Myszyniec  oma. Przewozy pasaerskie drog
        kolejow oma  Ostroka  oma funkcjonoway do 1 stycznia 1993
        roku. Tras t kursowao 5 par pocigw ze redni prdkoci 30 km/h, z
        ktrej korzystao rednio rocznie 9510 osb  prawie 5 razy za mao, by
        utrzyma rentowno.
      </h2>
    </BackgroundImage>
  )
}

export default Article4
